<template>
  <b-card no-body>
    <b-card-header class="align-items-baseline">
      <feather-icon
        icon="SettingsIcon"
        size="20"
        class="text-muted cursor-pointer"
      />
    </b-card-header>
    <b-row>
      <b-col cols="2">
        <statisticsCard
          :statistic="total"
          icon="SettingIcon"
          statistic-title="مجموع الطلبات"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="completed"
          icon="SettingIcon"
          statistic-title="الطلبات المنتهية"
          color="primary"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="rejected"
          icon="SettingIcon"
          statistic-title="الطلبات المرفوضه"
          color="primary"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="newOrders"
          icon="SettingIcon"
          statistic-title="الطلبات الجديدة"
          color="primary"
        />
      </b-col>
      <b-col cols="2">
        <statisticsCard
          :statistic="current"
          icon="SettingIcon"
          statistic-title="الطلبات الحالية"
          color="primary"
        />
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
import {
  BCard,
  BCardHeader,
  BRow,
  BCol,
} from 'bootstrap-vue'
import statisticsCard from '@core/components/statistics-cards/StatisticCardVertical.vue'

export default {
  components: {
    BCard,
    BCardHeader,
    statisticsCard,
    BRow,
    BCol,
  },
  data() {
    return {
      new: 0,
      current: 0,
      rejected: 0,
      total: 0,
      completed: 0,
      newOrders: 0,
    }
  },
  created() {
    this.$store.dispatch('common/fetchOrdersCountUser').then(res => {
      this.rejected = res.data.data.rejected
      this.total = res.data.data.total
      this.completed = res.data.data.completed
      this.current = res.data.data.current
      this.newOrders = res.data.data.new
    })
  },
}
</script>

